@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#app {
  max-width: 1200px;
  margin: 0 auto;
  padding: 60px 30px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

#app main {
  margin-left: 30px;
}

.navbar {
  display: flex;
  flex-direction: column;
  background-color: #5a2ea6;
}

.navbar .navbar-title {
  color: #fff;
  margin: 15px;
  text-decoration: none;
}

@media (max-width: 1000px) {
  #app {
    flex-direction: column;
  }

  #app main {
    margin-left: 0;
    margin-top: 30px;
    width: 100%;
  }
}

li.contract-item {
  background: #fff;
  box-shadow: 0 0 14px 0 rgba(0, 0, 0, 0.1);
  border-radius: 2px;
  padding: 20px;
}

li.contract-item header {
  display: flex;
  flex-direction: column;
  font-size: 16px;
  font-weight: bold;
}

.contract-info strong {
  display: block;
  font-size: 16px;
  color: #333;
}

.contract-info span {
  font-size: 13px;
  color: #999;
  margin-top: 2px;
}

li.contract-item p {
  color: #666;
  font-size: 14px;
  line-height: 20px;
  margin: 10px 0;
}

li.contract-item a {
  color: #8e4dff;
  font-size: 14px;
  text-decoration: none;
  -webkit-transition: color 0.5s;
  transition: color 0.5s;
  margin-top: 5px;
  float: right;
}

li.contract-item a:hover {
  color: #5a2ea6;
}

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
}

body {
  background: #e5e6f0;
  -webkit-font-smoothing: antialiased;
}

body,
input,
button {
  font-family: Roboto, sans-serif;
}

main {
  flex: 1 1;
}

main ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
  gap: 20px;
  list-style: none;
}

@media (max-width: 650px) {
  main ul {
    grid-template-columns: 1fr;
  }
}

